.main-page {
    background-color: #000000;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow-y: hidden;
    overflow-x: hidden;
  }
  
  .hexagon-container {
    background-image: url('./Hexagon.png');
    background-repeat: repeat;
    width: 100%;
    height: 100%;
    position: relative;
  }

  
  .marquee {
    position: absolute;
    top: 20px;  
    width: 100%;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    animation: scrolling 20s linear infinite;
    font-size: 20px;  
    font-weight: bold; 
    display: flex; 
  }
  
  @keyframes scrolling {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
  }